	module.exports = {"data":{"post":{"html":"<p>Yesterday Christopher picked out a <a title=\"Amazon.de: Bücher: Flip, der kleine Delphin\" href=\"http://www.amazon.de/exec/obidos/ASIN/3551086516/qid=1073768033\">picture book</a> from the <a href=\"http://www.carlsenkids.de/programm/lesemaus/\">Carlsen 'Lesemaus' series</a>. It is  about a baby dolphin, including pictures of the dolphin being born and nursing. OK, we can handle questions about that. The birth isn't so interesting for Christopher yet, but he noticed the nursing. 'The baby dolphin is drinking, like I used to drink from Mama!' Is his memory really that good? Not really... when I read to him this evening, he said he used to drink from Papa. Mama could only laugh.</p>","fields":{"title":"Flip the little Dolphin","date":"10 Jan 2004","url":"https://papascott.de/2004-01-10-flip-the-little-dolphin/"}}},"pathContext":{"slug":"/2004-01-10-flip-the-little-dolphin/","prev":{"fields":{"title":"Practice Fee","date":"09 Jan 2004","slug":"/2004-01-09-practice-fee/"}},"next":{"fields":{"title":"Two Polls","date":"10 Jan 2004","slug":"/2004-01-10-two-polls/"}}}}

